@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,400;1,200;1,300&display=swap);
/* base styles */
body {
  font-family: "Poppins", sans-serif;
  margin: 0;
  font-size: 1.5em;
  text-align: center;
  background: #000;
  color: #fff;
}

.App {
  max-width: 860px;
  margin: 40px auto;
}
button {
  background: none;
  border: 2px solid #fff;
  padding: 6px 12px;
  border-radius: 5px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
}
button:hover {
  background: #b6086d;
  color: #fff;
}
.card-grid{
  margin-top: 40px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap:20px;
}
.card img{
  height: 200px;
}

.card{
    position: relative;
}
.card img{
    width: 100%;
    display: block;
    border:2px solid #fff;
    border-radius: 6px;
}

/* card front - the picture */
.card .front{
    -webkit-transform: rotateY(90deg);
            transform: rotateY(90deg);
    transition: all ease-in 0.2s;
    position: absolute;
}

.flipped .front{
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
    transition-delay: 0.2s;
    pointer-events: none;
}

/* card back - the cover */
.card .back{
    transition:all ease-out 0.2s;
    transition-delay: 0.2s;
}

.flipped .back{
    -webkit-transform: rotateY(90deg);
            transform: rotateY(90deg);
    transition-delay: 0s;
    pointer-events: none;
}

